/* Projects.module.css */

.title{
    text-align: center;
    font-size: 1.5rem;
}
.title hr{
    width: 80px;
    padding: 0 30px;
    margin: 0 auto;
    border: 1px solid #000;
}
.title span{
    color: var(--brand-color);
}
.projects {
    background-color: #f4f4f4;
    padding: 80px 0;
  }
  

  .projectContent {
    text-align: center;
    margin-bottom: 40px;
  }
  
 
  
  .projectContent h3 {
    font-size: 16px;
    color: #333;
    margin: 20px 0;
  }
  
  
  
  /* Style for individual project items */
  .pro {
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: box-shadow 0.3s ease-in-out;
  }
  
  .pro:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
  
  .pro__img img {
    width: 100%;
    height: auto;
    transition: transform 0.3s ease-in-out;
  }
  
  .pro:hover .pro__img img {
    transform: translateY(-5%);
  }
  
  .pro__text {
    padding: 20px;
  }
  
  .pro__text h3 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .pro__text p {
    font-size: 16px;
    color: #666;
    margin-bottom: 20px;
  }
  
  .stack p {
    background-color: #333;
    color: #fff;
    display: inline-block;
    padding: 5px 10px;
    margin-right: 10px;
    margin-bottom: 10px;
  }
  
  .links a {
    color: #333;
    text-decoration: none;
    margin-right: 10px;
    display: inline-flex;
    align-items: center;
  }
  
  .links a:hover {
    color: #61dafb;
  }
  
  .tabler-icon {
    margin-right: 5px;
  }
  

  /* ProjectItem.module.css */

/* Style for individual project items */
.projectItem {
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: box-shadow 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .projectItem:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
  
  .projectItem img {
    width: 100%;
    height: auto;
    transition: transform 0.3s ease-in-out;
  }
  
  .projectItem:hover img {
    transform: translateY(-5%);
  }
  
  .projectItemContent {
    padding: 20px;
    flex: 1;
  }
  
  .projectItemContent h3 {
    font-size: 24px;
    color: #333;
    margin-bottom: 10px;
  }
  
  .projectItemContent p {
    font-size: 16px;
    color: #666;
    margin-bottom: 10px;
  }
  
  .projectItemContent a {
    color: #333;
    text-decoration: none;
    margin-right: 10px;
    display: inline-flex;
    align-items: center;
  }
  
  .projectItemContent a:hover {
    color: #61dafb;
  }
  
  .tabler-icon {
    margin-right: 5px;
  }
  